.education-section {
  background: #0e0e0e;
  color: white;
  padding: 60px 20px;
  text-align: center;
  border-top: 2px solid #00ffd5;
  overflow-x: auto;
}

.education-title {
  font-size: 2.5rem;
  color: #00ffd5;
  margin-bottom: 50px;
}

.education-horizontal {
  display: flex;
  gap: 30px;
  padding: 20px;
  justify-content: flex-start;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
}

.education-card-3d {
  background: #1e1e1e;
  border-radius: 16px;
  min-width: 280px;
  max-width: 300px;
  padding: 25px 20px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 255, 213, 0.15);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  transform-style: preserve-3d;
  perspective: 1000px;
  scroll-snap-align: start;
}

.education-card-3d:hover {
  transform: rotateY(5deg) scale(1.02);
  box-shadow: 0 15px 35px rgba(0, 255, 213, 0.3);
}

.edu-icon {
  font-size: 2rem;
  color: #eb97d9;
  margin-bottom: 10px;
}

.education-logo {
  width: 50px;
  height: 50px;
  object-fit: contain;
  background: white;
  padding: 4px;
  border-radius: 8px;
  margin: 0 auto 15px;
  display: block;
}

.education-content h3 {
  margin: 0;
  font-size: 18px;
  color: #3aee91;
}

.education-content p {
  font-size: 15px;
  color: #ccc;
  margin: 5px 0;
}

.education-content span {
  font-size: 13px;
  color: #888;
}
