.contact-section {
  background: linear-gradient(145deg, #0f2027, #203a43, #2c5364);
  color: #fff;
  padding: 5rem 2rem;
  text-align: center;
  position: relative;
  z-index: 1;
  border-top: 2px solid #00ffd5;
}

.contact-title {
  font-size: 3rem;
  margin-bottom: 3rem;
  color: #f581e1;
  text-shadow: 2px 2px 10px #0c7263;
}

.contact-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  perspective: 1000px;
}

.contact-card {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid #00ffd5;
  padding: 2rem;
  border-radius: 1.5rem;
  width: 220px;
  height: 200px;
  text-align: center;
  box-shadow: 0 8px 16px rgba(0, 255, 213, 0.2);
  backdrop-filter: blur(8px);
  transition: transform 0.4s ease-in-out;
  color: rgb(197, 171, 239);
}

.contact-card:hover {
  transform: rotateY(15deg);
}

.contact-card a {
  color: #70d5a1;
  text-decoration: none;
  margin-top: 0.5rem;
  display: inline-block;
}

.contact-card p {
  font-size: 1.2rem;
  margin: 0.5rem 0;
}
