.home-section {
  padding: 100px 20px;
  background: linear-gradient(145deg, #0f0f0f, #1a1a1a);
  color: #ffffff;
  text-align: center;
  font-family: 'Segoe UI', sans-serif;
  position: relative;
  overflow: hidden;
  border-top: 2px solid #00ffd5;
}

.home-content {
  max-width: 800px;
  margin: 0 auto;
  z-index: 1;
  position: relative;
}

.home-heading {
  font-size: 42px;
  margin-bottom: 20px;
  animation: fadeInDown 1.2s ease-out;
}

.highlight {
  color: #ff69b4;
}

.home-subtitle {
  font-size: 20px;
  color: #aaa;
  margin-bottom: 30px;
}

.icon-row {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin: 20px 0;
  animation: fadeInUp 1.5s ease;
}

.home-icon {
  font-size: 50px;
  color: #00ffd5;
  transition: transform 0.3s ease;
}

.home-icon:hover {
  transform: scale(1.2) rotate(10deg);
}

.home-tagline {
  font-size: 18px;
  color: #77ffdd;
  margin-top: 20px;
  animation: fadeIn 2s ease;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* NEW: Animated text */
.animated-title .char {
  display: inline-block;
  opacity: 0;
  transform: translateY(-20px);
  animation: letterFadeIn 0.4s ease-out forwards;
}

.animated-title:not(.animate) .char {
  animation: none !important;
}

@keyframes letterFadeIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
