/* Footer.css */

.footer {
  background:  #000;
  color: #220808;
  padding: 40px 20px 20px;
  font-family: 'Segoe UI', sans-serif;
  border-top: 2px solid #00ffd5;
  box-shadow: 0 0 20px rgba(0, 255, 213, 0.1);
}

.footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.footer-section {
  flex: 1;
  text-align: center;
}

.quote {
  font-style: italic;
  font-size: 1.1rem;
  color: #ccc;
}

.count {
  margin: 10px 0;
}

.label {
  font-size: 1.2rem;
  font-weight: bold;
  color: #00ffd5;
  margin-bottom: 10px;
}

.count-box {
  padding: 16px 30px;
  border-radius: 15px;
  background: linear-gradient(to right, #ee6843, #00b3ff);
  font-size: 2rem;
  font-weight: bold;
  color: #000;
  box-shadow: 0 0 20px rgba(0, 255, 213, 0.5);
  animation: pulseGlow 2.5s ease-in-out infinite;
  display: inline-block;
}

@keyframes pulseGlow {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 0 20px rgba(0, 255, 213, 0.4);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 30px rgba(0, 255, 213, 0.8);
  }
}

.fancy-icons {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 10px;
}

.fancy-icons a {
  color: #e65cf6;
  font-size: 1.6rem;
  transition: all 0.3s ease;
  position: relative;
}

.fancy-icons a:hover {
  color: #00ffd5;
  transform: scale(1.2);
  text-shadow: 0 0 12px #00ffd5;
}

/* Tooltip */
.fancy-icons a[title]::after {
  content: attr(title);
  position: absolute;
  bottom: -28px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.75rem;
  background: #000;
  color: #00ffd5;
  padding: 4px 8px;
  border-radius: 6px;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease;
  white-space: nowrap;
}

.fancy-icons a:hover::after {
  opacity: 1;
}

.footer-note {
  text-align: center;
  margin-top: 25px;
  font-size: 0.9rem;
  color: #fe53d6;
}

.footer-note .heart {
  color: red;
  animation: beat 1.5s infinite;
}

@keyframes beat {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
}

.footer-note .name {
  font-weight: bold;
  color: #00ffd5;
}

/* Responsive */
@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
