.more-about-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
  color: white;
  font-family: 'Segoe UI', sans-serif;
}

/* Header */
.more-about-page .intro-header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
  padding: 20px;
  flex-wrap: wrap;
}

.more-about-page .profile-img {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 50%;
  border: 3px solid #00ffd5;
  box-shadow: 0 0 15px rgba(0, 255, 213, 0.5);
  animation: floatImage 4s ease-in-out infinite;
}

.intro-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 60px 20px;
  background: radial-gradient(ellipse at center, #1a1a1a 0%, #0d0d0d 100%);
}

.intro-box {
  background: linear-gradient(135deg, #1f1f1f, #2a2a2a);
  border: 1px solid rgba(0, 255, 213, 0.3);
  border-radius: 20px;
  padding: 40px;
  max-width: 1000px;
  width: 100%;
  box-shadow: 0 0 20px rgba(0, 255, 213, 0.15), 0 0 40px rgba(255, 134, 212, 0.1);
  animation: floatBox 2s ease-in-out;
  color: #ffffff;
}

.big-title {
  font-size: 2.5rem;
  color: #00ffd5;
  margin-bottom: 20px;
  text-align: center;
}

.highlight {
  color: #ff86d4;
}

.intro-text {
  font-size: 1.1rem;
  line-height: 1.8;
  text-align: justify;
  padding: 0 10px;
}


/* Sections */
.more-about-page .content-section {
  margin-bottom: 60px;
}

.more-about-page .section-title {
  font-size: 2rem;
  color: #40e9e6;
  margin-bottom: 10px;
  text-align: center;
}

.more-about-page .section-desc {
  font-size: 1.1rem;
  text-align: center;
  margin-bottom: 20px;
}

.more-about-page .skills-list,
.more-about-page .info-list {
  max-width: 800px;
  margin: 0 auto 20px auto;
  list-style: disc;
  padding-left: 20px;
  font-size: 1rem;
  line-height: 1.6;
}

/* Image Slider */
.more-about-page .image-slider {
  display: flex;
  overflow-x: auto;
  gap: 20px;
  padding: 10px;
  scroll-behavior: smooth;
}

.more-about-page .slider-img {
  height: 240px;
  object-fit: contain;
  border-radius: 16px;
  cursor: pointer;
  transition: transform 0.3s ease;
  flex-shrink: 0;
  border: 2px solid transparent;
  background-image: linear-gradient(#0f2027, #2c5364), linear-gradient(45deg, #00ffd5, #ff86d4, #00ffd5);
  background-origin: padding-box, border-box;
  background-clip: padding-box, border-box;
  animation: floatImage 3s ease-in-out infinite;
}

.more-about-page .slider-img:hover {
  transform: scale(1.05);
  box-shadow: 0 0 25px rgba(0, 255, 213, 0.4);
}

/* Zoom Modal */
.more-about-page .zoom-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(15, 32, 39, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease;
}

.more-about-page .zoomed-image {
  max-width: 90%;
  max-height: 90%;
  border-radius: 16px;
  box-shadow: 0 0 30px rgba(255, 255, 255, 0.3);
}

/* Back Button */
.more-about-page .back-btn-wrapper {
  text-align: center;
  margin: 2rem 0;
}

.more-about-page .back-button {
  background: #00ffd5;
  color: #000;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background 0.3s ease;
  box-shadow: 0 0 10px #00ffd5;
}

.more-about-page .back-button:hover {
  background: #00c4a7;
  box-shadow: 0 0 15px #00ffd5;
}

/* Info Cards */
.more-about-page .info-icons-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.more-about-page .info-icon-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 20px;
  width: 200px;
  text-align: center;
  transition: transform 0.3s ease;
  backdrop-filter: blur(5px);
  box-shadow: 0 0 10px rgba(0, 255, 213, 0.1);
}

.more-about-page .info-icon-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 20px rgba(0, 255, 213, 0.3);
}

.more-about-page .icon-circle {
  font-size: 2rem;
  margin-bottom: 10px;
  color: #00ffd5;
}

.more-about-page .icon-title {
  font-size: 1.2rem;
  margin-bottom: 6px;
  color: #1ffb89;
}

.more-about-page .icon-text {
  font-size: 1rem;
  color: white;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes floatImage {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
