.tech-section {
  padding: 40px 20px;
  background: linear-gradient(135deg, #031d1f, #020a17);
  color: #f8fafc;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  perspective: 1000px;
  border-top: 2px solid #00ffd5;
}

.tech-title {
  font-size: 2.8rem;
  text-align: center;
  margin-bottom: 30px;
  color: #00ffd5;
  text-shadow: 0 0 10px #09afa7;
}

.skills-container {
  display: flex;
  gap: 20px;
  overflow-x: auto;
  padding-bottom: 10px;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
}

.skill-group {
  background: #022a32;
  padding: 20px 18px;
  border-radius: 16px;
  width: 280px;
  box-shadow:
    0 8px 15px rgba(14, 165, 233, 0.3),
    0 12px 30px rgba(14, 165, 233, 0.15);
  transform-style: preserve-3d;
  transition: transform 0.3s ease;
  flex-shrink: 0;
  cursor: default;
}

.skill-group:hover {
  transform: rotateY(10deg) rotateX(8deg) scale(1.05);
  box-shadow:
    0 12px 25px rgba(14, 165, 233, 0.5),
    0 20px 40px rgba(14, 165, 233, 0.3);
}

.group-title {
  font-size: 1.4rem;
  margin-bottom: 16px;
  color: #f06bf0;
  border-bottom: 3px solid #38f8ef;
  padding-bottom: 6px;
  display: flex;
  align-items: center;
  gap: 10px;
  text-shadow: 0 0 6px #360931;
}

.group-icon-img {
  width: 26px;
  height: 26px;
  filter: drop-shadow(0 0 2px #15fafa);
}

.skill-bar {
  margin-bottom: 14px;
  transition: transform 0.3s ease;
  border-radius: 14px;
  box-shadow:
    inset 0 0 6px #0ee9d0,
    0 0 5px #38f8e5;
  background: #17302f;
  transform-style: preserve-3d;
}

.skill-bar:focus,
.skill-bar:hover {
  transform: translateZ(12px) scale(1.04);
  box-shadow:
    inset 0 0 8px #00ffd5,
    0 0 10px #00ffd5,
    0 0 20px #00ffd5;
  outline: none;
  cursor: pointer;
}

.skill-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 6px 10px;
  font-weight: 600;
  font-size: 0.85rem;
  color: #e0e7ff;
  gap: 8px;
}

.skill-icon-img {
  width: 20px;
  height: 20px;
  border-radius: 4px;
  filter: drop-shadow(0 0 1px #38bdf8);
}

.skill-text-icon {
  width: 20px;
  height: 20px;
  font-size: 13px;
  font-weight: 700;
  text-align: center;
  line-height: 20px;
  color: #38bdf8;
  background-color: #1e293b;
  border-radius: 4px;
  box-shadow: 0 0 4px #0ea5e9 inset;
}

.skill-name {
  flex-grow: 1;
  margin-left: 6px;
  user-select: none;
}

.skill-level {
  font-weight: 700;
  color: #60a5fa;
  min-width: 38px;
  text-align: right;
  user-select: none;
}

.bar {
  height: 8px;
  border-radius: 14px;
  background: #334155;
  margin: 0 10px 8px 10px;
  box-shadow: inset 0 0 5px #0ea5e9;
}

.fill {
  height: 100%;
  background: linear-gradient(90deg, #28f093, #13a221);
  border-radius: 14px 0 0 14px;
  transition: width 1.2s ease-in-out;
}
