.internship-section {
  background: linear-gradient(145deg, #0f0f0f, #1c1c1c);
  color: white;
  padding: 60px 20px;
  text-align: center;
  font-family: 'Segoe UI', sans-serif;
  border-top: 2px solid #00ffd5;
}

.internship-title {
  font-size: 2.5rem;
  color: #00ffd5;
  margin-bottom: 40px;
  text-shadow: 0 0 10px #269885;
}

.internship-card,
.certification-section,
.workshop-section,
.achievement-section {
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid #00ffd59a;
  border-radius: 20px;
  box-shadow: 0 0 25px rgba(0, 255, 213, 0.15);
  margin: 20px auto;
  max-width: 700px;
  padding: 25px;
  backdrop-filter: blur(10px);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.internship-card:hover,
.certification-section:hover,
.workshop-section:hover,
.achievement-section:hover {
  transform: scale(1.03);
  box-shadow: 0 0 35px rgba(0, 255, 213, 0.25);
}

h3 {
  font-size: 1.6rem;
  color: #da9ce2;
  margin-bottom: 10px;
}

ul {
  list-style: none;
  padding-left: 0;
}

ul li {
  padding: 5px 0;
  font-size: 1rem;
  color: #ccc;
}
