.hero {
  position: relative;
  background-color: #0e0e0e;
  color: white;
  padding: 80px 20px;
  margin-top: 0;
  text-align: center;
  font-family: 'Segoe UI', sans-serif;
  overflow: hidden;
}

.hero-canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
}

.hero-content {
  position: relative;
  z-index: 1;
  max-width: 800px;
  margin: 0 auto;
}

.profile-img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 4px solid #00ffd5;
  margin-bottom: 20px;
  box-shadow: 0 0 20px rgba(0, 255, 213, 0.5);
}

.name-highlight {
  color: #00ffd5;
}

.subtitle {
  font-size: 18px;
  color: #bbb;
  margin-bottom: 30px;
}

.code-terminal {
  background-color: #1e1e1e;
  border-radius: 8px;
  padding: 20px;
  font-family: 'Courier New', monospace;
  color: #00ff9f;
  text-align: left;
  display: inline-block;
  animation: fadeInUp 1s ease-in-out;
}

.terminal-line {
  display: block;
  margin-bottom: 8px;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
