.projects-section {
  padding: 6rem 2rem;
  background: #0a0a0a; /* Slightly lighter than pure black */
  color: white;
  text-align: center;
  position: relative;
  overflow: hidden;
  z-index: 1;
  border-top: 2px solid #00ffd5;
}

.projects-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 200%;
  height: 200%;
  background: repeating-linear-gradient(
      90deg,
      rgba(0, 255, 213, 0.08) 0px,
      rgba(0, 255, 213, 0.08) 1px,
      transparent 1px,
      transparent 20px
    ),
    repeating-linear-gradient(
      0deg,
      rgba(0, 255, 213, 0.08) 0px,
      rgba(0, 255, 213, 0.08) 1px,
      transparent 1px,
      transparent 20px
    ),
    radial-gradient(circle at center, rgba(0, 255, 213, 0.08) 0%, transparent 80%);
  animation: matrixFlow 25s linear infinite;
  z-index: 0;
  pointer-events: none;
  opacity: 0.7;
}

@keyframes matrixFlow {
  0% {
    transform: translateY(0) rotate(0deg);
  }
  100% {
    transform: translateY(-100%) rotate(360deg);
  }
}

.projects-heading {
  font-size: 2.5rem;
  color: #00ffd5;
  font-weight: 800;
  text-shadow: 0 0 10px #07342c;
  margin-bottom: 2rem;
  opacity: 0;
  transform: translateY(-30px);
  animation: fadeInUp 1s ease forwards;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  max-width: 1000px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.project-card {
  background: #111;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 0 10px #00ffd5;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  z-index: 2;
}

.project-card:hover {
  transform: translateY(-10px) scale(1.03);
  box-shadow: 0 0 20px #00ffd5;
}

.project-image {
  width: 100%;
  height: 160px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.project-title {
  font-size: 1.2rem;
  color: #00ffd5;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.project-description {
  color: #ccc;
  font-size: 0.95rem;
}

.project-link {
  display: inline-block;
  margin-top: 1rem;
  color: #ef89b7 !important;
  font-weight: 600;
  text-decoration: none;
  transition: color 0.3s ease;
}

.project-link:hover {
  color: #33fff6;
}
