.about-section {
  padding: 6rem 2rem 4rem 2rem;
  background: #111;
  color: white;
  text-align: center;
  border-top: 2px solid #00ffd5;
}

.about-section h2 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: #00ffd5;
}

.about-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  max-width: 900px;
  margin: 0 auto;
}

.profile-img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid #00ffd5;
  box-shadow: 0 0 15px #00ffd5;
}

.about-text {
  max-width: 600px;
  text-align: left;
  font-size: 1.1rem;
  line-height: 1.6;
}
.more-info-btn {
  margin-top: 1.5rem;
  padding: 0.75rem 1.5rem;
  background-color: #dc11ee;
  color: #111;
  border: none;
  border-radius: 25px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
  box-shadow: 0 0 15px rgba(0, 255, 213, 0.4);
}

.more-info-btn:hover {
  background-color: #0ff;
  transform: scale(1.05);
  box-shadow: 0 0 25px rgba(0, 255, 213, 0.7);
}