.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  z-index: 1000;
  box-shadow: 0 0 10px rgba(0, 255, 213, 0.2);
}

.logo {
  font-size: 1.6rem;
  color: #00ffd5;
  font-weight: bold;
}

.nav-links {
  display: flex;
  align-items: center;
}

.nav-links a {
  margin-left: 1.5rem;
  color: white;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #00ffd5;
}

.resume-button {
  background-color: #00ffd5;
  color: #000;
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: bold;
  display: flex;
  align-items: center;
  transition: background 0.3s ease;
  margin-left: 1.5rem;
}

.resume-button:hover {
  background-color: #00e6c0;
  color: #000;
}

.hamburger {
  display: none;
  font-size: 1.5rem;
  color: white;
  cursor: pointer;
}

/* Responsive */
@media screen and (max-width: 768px) {
  .nav-links {
    position: absolute;
    top: 70px;
    right: 0;
    background-color: #0e0e0e;
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem 2rem;
    width: 220px;
    height: 100vh;
    transform: translateX(100%);
    transition: transform 0.3s ease;
  }

  .nav-links.active {
    transform: translateX(0);
  }

  .nav-links a {
    margin: 1rem 0;
    width: 100%;
  }

  .resume-button {
    width: 100%;
    justify-content: center;
    margin-top: 1rem;
  }

  .hamburger {
    display: block;
  }
}
